import React from 'react';
import AdSense from 'react-adsense';

const ArticleInlineAd = (props) => {
  return (
    <AdSense.Google
        client='ca-pub-5715094337709253'
        slot='6027229091'
        style={{ display: 'block' }}
        layout='in-article'
        format='fluid'
        />
  );
};

export default ArticleInlineAd;